.login-body{
    background-image: url('../../img/bg_login.jpg');
    background-size: cover;
    background-position: center;

    header{
        background: transparent;
        position: absolute;
    }
}

.login-wrap{
    padding-top: 80px;

    .board{
        box-shadow: 0 0 20px 10px rgba(0,0,0,0.1);
        padding:30px 40px;
    }
    
    h1{
        text-align: center;
    }
}
@media (max-width: $device-sm) {
    .login-body header{
        ul.menu{
            margin-top: -5px;
            margin-right: -20px;
        }
        
        .burger{
            display: none !important;
        }
    }
}

